}



@include for-narrow-layout {
    .enjoyable-container {
        display: flex;
        flex-direction: column;
    }

    .nature-img {
        width: 100%;
        max-width: rem(573px);
        max-height: rem(360px);
    }
    .enj-box2 {
        border: 1px solid red;
        left: rem(100px);
        top: rem(270px);
    }

    .divide {
       left: rem(0px);
    }
}

@include for-mobile-layout {
    .nature-img {
        content:url("../../assets/imgs/homepage/enjoyable-place-mobile.jpg");
    }

